* {
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif !important;
}

body {
  background: linear-gradient(to right, #2c68b8, #92e4f3);
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  min-height: 100%;
}

::placeholder {
  color: #666464 !important;
  font-weight: bold;
}

.divContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  color: white;
}

.boxContainer {
  min-height: 300px;
  width: 75%;
  background-color: white;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.boxContainer h2 {
  color: #4854ef;
  font-weight: 500;
}

.formBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100px;
  justify-content: space-between;
}

.formBox input,
button {
  width: 90%;
}

.formBox button b {
  font-size: 12px;
}

.divError {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  background-color: #dba29a;
  height: 40px;
}

.divError span {
  color: #772e25;
  font-weight: bold;
}

.formContainer{
  display: flex;
  flex-direction: row;
  min-height: 300px;
  width: 75%;
  background-color: white;
  border-radius: 20px;
  align-items: flex-start;
  justify-content: space-evenly;
  flex-wrap: wrap;
  
  
}

.agregarGastoDiv{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 275px;
  flex-grow: 4;
  height: 100%;
  margin-top: 3%;
  


}

.agregarGastoDiv form{
  width: 100%;
}

.listadoDiv{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 275px;
  margin-top: 3%;
  
  flex-grow: 5;


}

.ul-list{
  width: 90%;
}

.li-item{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.higlight{
  background-color: rgb(33, 31, 148);
  color: white;
  font-weight: bold;
  width: 20%;
  text-align: center;
  border-radius: 3px;
  padding: 5px;
}


.divPresupuesto{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  background-color: #a2c8ff;
  height: 40px;
}

.divPresupuesto span{
  color:#2346a6;
  font-weight: bold;
  padding: 2px;
}

.divRestante.verde{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  background-color:#b1d8b7;
  height: 40px;
}

.divRestante.verde span{
  color: 	#2f5233;
  font-weight: bold;
}

.divRestante.amarillo{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  background-color:#fff2be;
  height: 40px;
}

.divRestante.amarillo span{
  color: #d0ae0c;
  font-weight: bold;
}

.divRestante.rojo{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  background-color: #dba29a;
  height: 40px;
}

.divRestante.rojo span{
  color: #772e25;
  font-weight: bold;
}

label{
  font-weight: bold;
  
}

button b{
  font-weight: bold;
  font-size: 12px;
}
#listado-component{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  min-width: 275px;
  margin-top: 3%;
  flex-grow: 1;
}
/* Add a hover style */
#delete-button :hover{
  cursor: pointer;

}


